import React, { Component } from 'react';
import axios from "axios";
import Home from "./components/Home";
import NavBar from "./components/Navbar";
import Profile from "./containers/Profile";
import { Switch, Route, Redirect } from "react-router-dom";

class App extends Component {
    constructor(props) {
        super(props);
        let tempstate = {
            loggedIn: true,
            error: "",
            loginPending: false,
            user: {
                id: 8,
                createdAt: "2018-04-19T18:41:40Z",
                updatedAt: "2018-04-19T20:31:02Z",
                email: "daryan.sankar1@gmail.com",
                firstname: "Daryan",
                lastname: "Sankar",
                gender: "male",
                birthdate: "1960-07-16T00:00:00Z",
                adminLevel: "user",
                about: "",
                profileImageURL: "",
                tags: [],
                invitations: [],
                requests: [],
                flags: []
            },
            token: "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhZG1pbiI6InVzZXIiLCJlbWFpbCI6ImRhcnlhbi5zYW5rYXIxQGdtYWlsLmNvbSIsImV4cCI6MTUyNDI2ODU3NSwiaWQiOjh9.auFedqmgCDwzWjG2KFDJ95BtP2abS95ww5naZUnPOjs"
        }
        this.state = {
            loggedIn: false,
            loginPending: false,
            error: "",
            registrationSuccess: false
            //...tempstate
        }
        this.confirmEmail = this.confirmEmail.bind(this);
        this.login = this.login.bind(this);
        this.logout = this.logout.bind(this);
    }

    login(info) {
        this.setState({loginPending: true});
        axios.post("https://roommates40plus.com/api/auth/login", info)
        .then(res => {
            this.setState({
                user: res.data.user,
                token: res.data.token,
                loggedIn: true,
                loginPending: false
            })
        }).catch(err => {
            this.setState({
                error: err.response.data.message,
                loginPending: false
            })
        })
    }

    logout() {
        this.setState({
            token: undefined,
            user: undefined,
            loggedIn: false
        })
    }

    confirmEmail(append) {
        this.props.history.push({pathname: this.props.location.pathname, search:""})
        axios.get("https://roommates40plus.com/api/auth/signup/confirmation" + append)
        .then(res => {
            this.setState({
                registrationSuccess: true
            })
        })
        .catch(err => {
            this.setState({
                error: "Failed to confirm registration."
            })
        })
    }

    render() {
        return (
            <Switch>
                <NavBar login={this.login} logout={this.logout} loggedIn={this.state.loggedIn} pending={this.state.loginPending}>
                    <Switch>
                        <Route exact path="/" render={() => this.state.loggedIn ? <Redirect to="/profile"/>:<Home confirmEmail={this.confirmEmail} 
                            error={this.state.error} success={this.state.registrationSuccess}/>}/>
                        <Route path="/profile" render={() => <Profile user={this.state.user}/>}/>
                    </Switch>
                </NavBar>
            </Switch>
        );
    }
}

export default App;



