import React from "react";
import { withRouter } from "react-router-dom";
import Signup from "../containers/Signup";
import logo from "../res/roommates40plusIcon.jpg";
//import twoWomen from "../res/Two-women-coffee.jpg";

const Home = props => {
    let append = props.location.search;
    if (append !== "") {
        props.confirmEmail(append);
    }
    return (
        <div className="container-fluid" style={{ marginTop: "52px" }}>
            <div className="container">
                <div className="row">
                    <div className="col-md-12" style={{textAlign:"center"}}>
                        Ads
                    </div>
                </div>
                {
                    props.error ?
                    <div className="alert alert-danger alert-dismissible">
                        <a href="#" className="close" data-dismiss="alert" aria-label="close">&times;</a>
                        <strong>Error!</strong> {props.error}
                    </div>:null
                }
                {
                    props.success ?
                    <div id="registration-success-alert" className="alert alert-success alert-dismissible">
                        <a href="#" className="close" data-dismiss="alert" aria-label="close">&times;</a>
                        <strong>Welcome!</strong> Your account registration is confirmed.
                    </div>:null
                }
                <div className="row" style={{padding:"0 0 10px 0"}}>
                    <div className="col-md-8">
                        <div style={{ textAlign: "center", margin: "4px 0", backgroundColor: "#527591", color: "white" }}>
                            <h4 style={{ padding: "6px" }}>Welcome</h4>
                        </div>
                        <img src={logo} width="100%" alt="Roommates 40+ logo" />
                    </div>
                    <div className="col-md-4">
                        <div style={{ textAlign: "center", margin: "4px 0", backgroundColor: "#527591", color: "white" }}>
                            <h4 style={{ padding: "6px" }}>Create a new account</h4>
                        </div>
                        <Signup />
                    </div>
                </div>
                <div className="row" style={{padding:"10px 0"}}>
                    <div className="col-md-12">
                        <div style={{ textAlign: "center", margin: "4px 0", backgroundColor: "#527591", color: "white" }}>
                            <h4 style={{ padding: "6px" }}>About Us</h4>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(Home);


