import axios from "axios";

export const START_USER_REQUEST = "START_USER_REQUEST";
export const startUserRequest = () => {
    return { type: START_USER_REQUEST }
}

export const LOGIN_SUCCESS = "LOGIN_SUCCESS";
export const loginSuccess = (user, token) => {
    return { type: LOGIN_SUCCESS, user, token }
}

export const LOGIN_FAILURE = "LOGIN_FAILURE";
export const loginFailure = error => {
    return { type: LOGIN_FAILURE, error }
}

export const login = (email, password) => {
    return dispatch => {
        dispatch(startUserRequest());
        axios.post("https://roommates40plus.com/api/auth/login", {
            email,
            password
        })
        .then(res => {
            console.log("login:", res)
            dispatch(loginSuccess(res.data.user, res.data.token))
        })
        .catch(err => {
            console.log("login:", err)
            dispatch(loginFailure(err))
        })
    }
}

export const SIGNUP_SUCCESS = "SIGNUP_SUCCESS";
export const signupSuccess = () => {
    return { type: SIGNUP_SUCCESS }
}

export const SIGNUP_FAILURE = "SIGNUP_FAILURE";
export const signupFailure = error => {
    return { type: SIGNUP_FAILURE, error }
}

export const signup = info => {
    return dispatch => {
        dispatch(startUserRequest());
        axios.post("https://roommates40plus.com/api/auth/signup", info)
        .then(res => {
            console.log("signup:", res)
            dispatch(signupSuccess())
        })
        .catch(err => {
            console.log("signup:", err)
            dispatch(signupFailure(err))
        })
    }
}



